.App {
  text-align: center;
  width:100vw;
  overflow: hidden;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.appContentContainer{
  width:100vw;
  display: flex;
  flex-direction:row;
}

.routesContainer{
width:100vw;
  overflow-x: hidden;
  position: relative;
  flex:1;
  /*background:red;*/
  height:100vh;
  
  /*width: calc(100vw - var(--grid-offset));*/
  /*background:red;*/
  padding:0 var(--grid-offset) 0 var(--grid-offset);
  box-sizing: border-box;
}


*{
  transition:all 0.3s ease;
}

.loading{
  position:fixed;
  top:0;
  left:0;
  bottom:0;
  right:0;
  background:#ffffff88;
}
